* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'roboto', sans-serif;
    font-size: 16px;
    font-weight: 400;
    scroll-behavior: smooth;
}

body {
    color: #000;
    display: flex;
    flex-direction: column;
    position: relative;
}

/*Header*/
#header {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    background-color: #ffffff;
}

.logo {
    width: 200px;
    height: 100px;
}

.search_area {
    flex-basis: 50%;
    border-radius: 0.3em;
}

.search_input {
    padding: 0.7rem 1rem;
    background: #ececf3;
    box-shadow: 0 1px 3px rgba(238, 238, 238, 0.7);
    width: 100%;
    height: 40px;
    border: none;
    border-radius: 0.3em;
}

.btn_wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 1em;
}

.account_area, .basket_area {
    width: 40px;
    height: 40px;
    background: #289d72;
    color: white;
    box-shadow: 0 0 2px rgba(40, 157, 114, 0.2);
    border: none;
    cursor: pointer;
    transition: 0.2s ease-out;
    border-radius: 0.3em;
}

.basket_area span{
    margin-left: 2px;
}

.account_area:hover, .basket_area:hover {
    background: #228361;
}

#count-items {
    font-size: 12px;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.2);
}

/*Main section*/
#main {
    display: flex;
    flex-direction: column;
}


.swiper {
    width: 100%;
    height: 500px;
}


.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.swiper-button-next, .swiper-button-prev {
    color: #31C48D;
}

.swiper-pagination-bullet {
    background-color: #31C48D;
    width: 10px;
    height: 10px;
}

.title {
    font-size: 32px;
    font-weight: 900;
    padding: 35px 0 15px 68px;
    color: #289d72;
    text-shadow: 0 0 1px rgba(40, 157, 114, 0.2);
}

.cards {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: stretch;
    padding: 0 68px 0 68px;
    gap: 1rem;
    margin: 10px 0 40px 0;
}

.cards-item {
    display: flex;
    flex-direction: column;
    width: 240px;
    border: 1px solid #ececf3;
    box-shadow: 0 1px 3px rgba(238, 238, 238, 0.7);
    padding: 10px;
    border-radius: 0.3em;
}

.cardImageArea {
    position: relative;
}

.card-image {
    width: 100%;
    height: 210px;
    object-fit: cover;
    filter: brightness(70%);

}

.discount_span {
    position: absolute;
    top: 10px;
    left: 10px;
    min-width: 35px;
    background-color: #289d72;
    box-shadow: 0 0 2px rgba(49, 196, 141, 0.2);
    color: #ffffff;
    padding: 5px;
    text-align: center;
    text-shadow: 0 0 2px rgba(255, 255, 255, 0.2);
    border-radius: 0.3em;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 1px;
}

.add-to-basket-btn {
    position: absolute;
    bottom: 15px;
    right: 10px;
    width: 35px;
    height: 35px;
    align-items: center;
    background: #289d72;
    box-shadow: 0 0 2px rgba(49, 196, 141, 0.2);
    border: none;
    border-radius: 0.3em;
    color: #fff;
    cursor: pointer;
    transition: 0.2s ease-out;
}

.add-to-basket-btn:hover {
    background-color: #228361;
}

.qv-btn {
    position: absolute;
    top: 90px;
    left: 60px;
    background-color: transparent;
    color: #ffffff;
    text-shadow: 0 0 2px rgba(255, 255, 255, 0.2);
    border: 2px solid #289d72;
    padding: 5px 10px;
    border-radius: 0.3em;
    cursor: pointer;
    transition: 0.2s ease-out;
}

.qv-btn:hover {
    background-color: #228361;
    border: 2px solid #228361;
}

.basket-svg {
    width: 20px;
    height: 20px;
    margin-right: 5px;
}

.cardDetailsArea {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 0.5em;
    margin-top: 6px;
}

.price-wrapper {
    display: flex;
    justify-content: space-between;
}

.price-discounted {
    font-weight: bold;
    color: #289d72;
    text-shadow: 0 0 1px rgba(49, 196, 141, 0.2);
}

.price-normal {
    color: #8c8b8b;
    text-decoration: line-through;
    font-size: 14px;
}

.card-title {
    font-weight: bold;
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
}

/*Footer*/
#footer {
    display: flex;
    flex-direction: column;
    background: #ececf3;
    box-shadow: 0 1px 3px rgba(238, 238, 238, 0.7);
    padding-top: 20px;
}

.footer_area {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.footer-list h4 {
    font-weight: bold;
    margin-bottom: 12px;
}

.footer-list li{
    list-style-type: none;
    margin-bottom: 8px;
}

a:active,
a {
    text-decoration: none;
    color: black;
}

a:hover {
    text-decoration: underline;
}

.footer_area img {
    width: 80px;
    height: 80px;
    margin-left: 24px;
}

.copyright {
    font-size: 13px;
    text-align: center;
    margin-top: 20px;
    background-color: black;
    color: #ececf3;
    padding: 10px 0 10px 0;
}

/*Modal*/
.qv-modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
}

.qv-modal-content {
    background-color: #fff;
    padding: 36px 36px 20px 36px;
    border-radius: 0.3em;
    max-width: 500px;
    text-align: center;
    position: relative;
}

.qv-card-image {
    width: 100%;
    max-height: 500px;
    object-fit: cover;
    margin-top: 3px;
    margin-bottom: 10px;
}

.qv-close-btn {
    position: absolute;
    top: 2px;
    right: 10px;
    cursor: pointer;
    font-size: 30px;
    font-weight: bold;
    color: #289d72;
    box-shadow: 0 0 2px rgba(49, 196, 141, 0.2);
}

.qv-close-btn:hover {
    color: #228361;
}

.qv-card-details h2 {
    margin-bottom: 18px;
    font-size: 20px;
    font-weight: bold;
    background-color: #289d72;
    box-shadow: 0 0 2px rgba(49, 196, 141, 0.2);
    color: #ffffff;
    padding: 3px;
}

.qv-card-details span {
    display: block;
    margin-bottom: 10px;
}

.qv-discounted-price {
    text-decoration: underline;
    font-weight: bold;
}

.qv-discount {
    color: #289d72;
    font-weight: bold;
    font-size: 18px;
}

.qv-price {
    text-decoration: line-through;
    color: #8c8b8b;
}

.notification {
    position: fixed;
    bottom: 2%;
    left: 50%;
    transform: translateX(-50%);
    background-color: #228361;
    color: #ffffff;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.2);
    padding: 10px 20px;
    border-radius: 0.3em;
    z-index: 3;
}

.basket-modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 5;
}

.basket-modal-content {
    position: absolute;
    top: 40px;
    right: 64px;
    padding: 38px;
    width: 400px;
    min-height: 550px;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.2);
    border-radius: 0.3em;
    background-color: #ffffffff;
    display: flex;
    flex-direction: column;
    gap: 1.5em;
}

.close-btn {
    position: absolute;
    top: 2px;
    right: 10px;
    cursor: pointer;
    font-size: 30px;
    font-weight: bold;
    color: #289d72;
    box-shadow: 0 0 2px rgba(49, 196, 141, 0.2);
}

.close-btn:hover {
    color: #228361;
}

.basket-modal-content h2 {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    background-color: #289d72;
    box-shadow: 0 0 2px rgba(49, 196, 141, 0.2);
    color: #ffffff;
    padding: 8px;
    border-radius: 0.3em;
}

.empty-basket-text {
    text-align: center;
    font-size: 18px;
    padding-top: 10px;
}

.basket-list-wrapper {
    flex-grow: 1;
    margin: 0;
}

.basket-items-list {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 0.4em;
}

.basket-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.basket-item-name {
    flex-basis: 35%;
}

.basket-item-price {
    flex-basis: 35%;
    text-align: end;
    padding-right: 16px;
}

.basket-footer-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1em;
    align-content: flex-end;
}

.total-price {
    margin-top: 20px;
    font-weight: bold;
    text-align: center;
    padding: 8px 16px;
    border-radius: 0.3em;
    border: 1px solid #289d72;
}

.basket-modal-content button {
    padding: 8px 16px;
    cursor: pointer;
    background: #289d72;
    box-shadow: 0 0 2px rgba(49, 196, 141, 0.2);
    border: none;
    border-radius: 0.3em;
    color: #fff;
    transition: 0.2s ease-out;
}

.basket-modal-content button:hover {
    background-color: #228361;
}

.clear-basket-btn {
    margin-top: 10px;
    width: 100%;
}

.login-modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 6;
}

.login-modal-content {
    background-color: #fff;
    padding: 26px;
    border-radius: 0.3em;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.2);
    position: relative;
    width: 400px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

form {
    display: flex;
    flex-direction: column;
    padding: 12px;
}

input[type="email"],
input[type="password"]  {
    background: #ececf3;
    box-shadow: 0 1px 3px rgba(238, 238, 238, 0.7);
    border: none;
    border-radius: 0.3em;
    margin-bottom: 20px;
    padding: 12px;
}

.login-close-btn {
    position: absolute;
    top: 2px;
    right: 10px;
    cursor: pointer;
    font-size: 30px;
    font-weight: bold;
    color: #289d72;
    box-shadow: 0 0 2px rgba(49, 196, 141, 0.2);
}

.login-close-btn:hover {
    color: #228361;
}

.login-button {
    padding: 12px;
    cursor: pointer;
    background: #289d72;
    box-shadow: 0 0 2px rgba(49, 196, 141, 0.2);
    border: none;
    border-radius: 0.3em;
    color: #fff;
    transition: 0.2s ease-out;
}

.login-button:hover {
    background-color: #228361;
}


